import { Box } from "@mui/material";
import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import StarIcon from "@mui/icons-material/Star";

const DifficultyLevelCard = ({ content, gametype, borderColor }) => {
  const url = `/game/${gametype}`;

  const ShowStar = ({ level }) => {
    return (
      <Fragment>
        <StarIcon sx={{ color: "#FFEA2D" }} />
        {level === "middle" && <StarIcon sx={{ color: "#FFEA2D" }} />}
        {level === "high" && (
          <Fragment>
            <StarIcon sx={{ color: "#FFEA2D" }} />
            <StarIcon sx={{ color: "#FFEA2D" }} />
          </Fragment>
        )}
      </Fragment>
    );
  };

  return (
    <Link
      to={{
        pathname: url,
      }}
      state={{ level: content.level }}
      style={{ textDecoration: "none", color: "inherit" }}
    >
      <Box
        sx={{
          border: `1px solid ${borderColor}`,
          padding: "0.5rem 1rem",
          margin: "0.2rem",
          borderRadius: "6px",
          boxSizing: "border-box",
          backgroundColor: "#FFFFFF",
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-around",
          alignItems: "center",
          width: "150px",
          height: "32px",
          // "&:hover": {
          //   backgroundColor: "#BFEAF5",
          //   opacity: [0.9, 0.8, 0.7],
          // },
        }}
      >
        <Box>{content.title}</Box>

        <Box
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <ShowStar level={content.level} />
        </Box>
      </Box>
    </Link>
  );
};

export default DifficultyLevelCard;
