import { Box, Button, Grid, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import ItemCard from "../Components/ItemCard";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

const Memory = () => {
  const navigate = useNavigate();

  const randomcube = (w, h, bool) => {
    let heightArr = [];

    const randomBool = () => Math.round(Math.random());

    for (let index = 0; index < h; index++) {
      let widthArr = [];
      for (let index = 0; index < w; index++) {
        widthArr.push(bool ? randomBool() : 0);
      }
      heightArr.push(widthArr);
    }

    return heightArr;
  };

  const [curCube, setCurCube] = useState(randomcube(4, 4, true));
  const [tempCube, setTempCube] = useState(randomcube(4, 4, false));
  const [checkCube, setCheckCube] = useState(false);

  const flatten = (arr) =>
    arr.reduce((acc, cur) => {
      return acc.concat(cur);
    });

  const [show, setShow] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setShow(false);
    }, 1000);

    return () => {
      clearTimeout();
    };
  }, []);

  const handleClick = (e) => {
    const x = e.target.getAttribute("rowindex");
    const y = e.target.getAttribute("columindex");

    const copyArr = [...tempCube];

    if (copyArr[x][y] === 0) {
      copyArr[x][y] = 1;
    } else {
      copyArr[x][y] = 0;
    }

    setTempCube(copyArr);
    check();
  };

  const check = () => {
    if (flatten(curCube).toString() === flatten(tempCube).toString()) {
      setShow(true);
      setCheckCube(true);
    }
  };

  return (
    <Box
      sx={{
        marginTop: 15,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      {show && !checkCube ? (
        <Typography component="h1" variant="h5" sx={{ marginBottom: 10 }}>
          .
        </Typography>
      ) : (
        <Button
          sx={{
            marginBottom: 10,
            color: 'black',
            background: "#EDEDED",
            boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
            borderRadius: "10px",
          }}
 
          onClick={() => navigate(-1)}
        >
          <ArrowBackIcon />
        </Button>
      )}

      <Grid
        container
        sx={{
          justifyContent: "center",
          alignItems: "center",
          bgcolor: "gray",
          width: "300px",
          height: "300px",
          borderRadius: "0.8rem",
        }}
      >
        {show
          ? curCube.map((row, i) => {
              return (
                <Grid item key={i}>
                  <ItemCard data={row} rowindex={i} />
                </Grid>
              );
            })
          : tempCube.map((row, i) => {
              return (
                <Grid item key={i}>
                  <ItemCard data={row} rowindex={i} handleClick={handleClick} />
                </Grid>
              );
            })}
      </Grid>
      {checkCube && (
        <Box
          sx={{
            fontSize: "2rem",
            marginTop: 2,
          }}
        >
          !
        </Box>
      )}
    </Box>
  );
};

export default Memory;
