import { Box } from "@mui/material";
import React from "react";

const ItemCard = ({ data, rowindex, handleClick }) => {
  return (
    <Box>
      {data.map((item, i) => {
        return (
          <Box
            key={i}
            onClick={handleClick ? handleClick: undefined}
            rowindex={rowindex}
            columindex={i}
            sx={{
              margin: "0.3rem",
              bgcolor: `${item === 0 ? "white" : "coral"}`,
              width: "50px",
              height: "50px",
              borderRadius: "0.3rem",
            }}
          />
        );
      })}
    </Box>
  );
};

export default ItemCard;
