import { Box, Stack } from "@mui/material";
import React from "react";
import DifficultyLevelCard from "./DifficultyLevelCard";

const GameTypeCard = ({ content }) => {
  let color;
  let borderColor;

  switch (content.url) {
    case "memory":
      color = "#FFECEC";
      borderColor = "#FF7E7E";
      break;
    case "logic":
      color = "#E7FFE3";
      borderColor = "#33BF1C";

      break;
    case "concentration":
      color = "#F1F2FF";
      borderColor = "#7C86FF";

      break;
  }

  return (
    <Box
      sx={{
        padding: 1,
        margin: "1rem",
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent:"space-around",
        backgroundColor: color,
        borderRadius: "15px",
        boxShadow : "0px 4px 4px rgba(0, 0, 0, 0.25)"
      }}
    >
      <Box
        sx={{
          padding: 5,
        }}
      >
        {content.title}
      </Box>
      <Stack>
        {content.level.map((item, i) => {
          return (
            <DifficultyLevelCard
              key={i}
              content={item}
              gametype={content.url}
              borderColor={borderColor}
            />
          );
        })}
      </Stack>
    </Box>
  );
};

export default GameTypeCard;
